.o-behave{
    display: flex;
    justify-content: center;
    width: 100%;
    margin: -1rem 5rem;
    transition: ease-out all .3s;
}
.o-behaveTitle{
    margin: 1rem 0;
}

.o-behaveimg{
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    position: relative;
    top: 20px;
    right: 5rem;
    width: 10rem;
    height: 5rem;
    
}

.o-behaveimg h3{
    width: 600px;
    font-weight: 300;
    margin: 0 1rem;
    border-radius: 10px;
}

.o-behaveimg img{
    width: 50%;    
    object-fit: contain;
    transition: .3s;
}

.o-behaveimg img:hover{
    width: 60%;
    transition: .3s;
}

@media only screen and (max-width:768px){
    .o-behaveimg h3{
        position: relative;
        top: 1.5rem;
        width: 300px;
        font-size: 14px;
    }

    .o-behaveimg img{
        position: relative;
        top: 2rem;
        width: 33%;
        object-fit: contain;
    }

}