* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  width: 100%;
  height: 100%;
  background-color: #212121;
  color: #fff;
  overflow: hidden;
}
.o-container {
  margin: 1rem auto;
  text-align: center;
  transition: ease-out all 0.3s;
}

.o-container h1 {
  margin: 2rem 0;
}

.o-container .o-play {
  margin: 10rem 0;
}

.o-container .o-cards {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
  margin: -3rem 18rem;
}

.o-container button {
  margin: -10rem 0;
  font-family: "Montserrat", sans-serif;
  width: 68%;
  height: 4rem;
  border: 1px solid;
  background: none;
  overflow: hidden;
  color: #fff;
  font-size: 20px;
  border-radius: 10px;
  transition: ease-out all 0.2s;
  outline: none;
}

.o-container button:hover {
  background-color: #0067b0;
  cursor: pointer;
  transform: scale(1.2);
  transition: 0.2s;
}


@media only screen and (max-width:768px){
  .o-container h1{
    font-size: 1rem;
  }
  .o-container .o-cards{
    margin: 0.1rem;
  }

  .o-container button {
    position: relative;
    bottom: -1rem;
    font-family: "Montserrat", sans-serif;
    width: 68%;
    height: 2rem;
    border: 1px solid;
    background: none;
    overflow: hidden;
    color: #fff;
    font-size: 20px;
    border-radius: 10px;
    transition: ease-out all 0.2s;
    outline: none;
  }
}