.o-card{
    width: 35rem;
    height: 18rem;
    margin: 6rem 3rem;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(255, 255, 255, 0.2);
    background-color: #fff;
    transition: ease-out all .3s;
}

.o-card:hover{
    transform: scale(1.05);
    transition: .2s;
    box-shadow: 0 8px 10px 5px rgba(255, 255, 255, 0.2);
}

.o-card h1{
    color: #000;
}


.o-descCard{
    width: 22rem;
    height: 8rem;
    margin: 2rem;
    color: #000;
    text-align: justify;
    word-break: normal;
}

@media only screen and (max-width:768px){
    .o-card{
        width: 15rem;
        height:10rem;
        margin: 6rem 0.4rem;
    }
    .o-card h1{
        position: relative;
        bottom: 20px;
        font-size: 1rem;
    }

    .o-descCard{
        position: relative;
        left: 15px;
        bottom: 36px;
        width: 9rem;
        font-size: 10px;
        margin: 5px;
        text-align: justify;
        word-break: normal;
    }
}
